#skills{
    color: white;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../img/bg-2.jpg');
    padding-top: 10rem;
    padding-bottom: 10rem;
}
#skills h1{
    font-weight: bold;
    font-size: 3.5rem;
    display: block;
    z-index: 1;
}
#skills h2{
    z-index: 1;
}

#skills .skill .row{
    width: 80vw;
    text-align: center;
}
#skills .skill p{
    margin: 0;
}
#skills .skill p:nth-of-type(1){
    width: 10%;
    margin: 0px 30px;
}
#skills .skill p:nth-of-type(2){
    background-color: rgba(0,214,70,1);
    vertical-align: middle;
    height: 15px;
}
#skills .skill p:nth-of-type(3){
    width: 5%;
    margin: 0px 30px;
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
    #skills .skill p:nth-of-type(1){
        width: 15%;
        margin: 0px 20px;
    }
    #skills .skill p:nth-of-type(2){
        background-color: rgba(0,214,70,1);
        vertical-align: middle;
        height: 15px;
    }
    #skills .skill p:nth-of-type(3){
        width: 5%;
        margin: 0px 20px;
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    #skills .skill p:nth-of-type(1){
        width: 15%;
        margin: 0px 15px;
    }
    #skills .skill p:nth-of-type(2){
        background-color: rgba(0,214,70,1);
        vertical-align: middle;
        height: 15px;
        text-align: center;
        margin: 0px 10px;
    }
    #skills .skill p:nth-of-type(3){
        width: 5%;
        margin: 0px 10px;
    }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
    #skills{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #skills .skill .row{
        width: 100%;
        text-align: center;
    }
    #skills .skill p:nth-of-type(1){
        width: 100%;
        margin: 0px 10px;
        text-align: left;
    }
    #skills .skill p:nth-of-type(2){
        background-color: rgba(0,214,70,1);
        vertical-align: middle;
        height: 15px;
        text-align: center;
        margin: 0px 10px;
    }
    #skills .skill p:nth-of-type(3){
        width: 10%;
        margin: 0px 10px;
    }
}

@media only screen and (max-width: 576px) {
    #skills{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #skills h1{
        font-weight: bold;
        font-size: 3rem;
        display: block;
        z-index: 1;
    }
    #skills h2{
        z-index: 1;
        font-size: 1.5rem;
    }
    #skills .skill .row{
        width: 100%;
        text-align: center;
    }
    #skills .skill p:nth-of-type(1){
        width: 100%;
        margin: 0px 10px;
        text-align: left;
    }
    #skills .skill p:nth-of-type(2){
        background-color: rgba(0,214,70,1);
        vertical-align: middle;
        height: 15px;
        text-align: center;
        margin: 0px 10px;
    }
    #skills .skill p:nth-of-type(3){
        width: 10%;
        margin: 0px 10px;
    }
}