#portfolio{
    color: white;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../img/bg-3.jpg');
    padding-top: 10rem;
    padding-bottom: 10rem;
}
#portfolio h1{
    font-weight: bold;
    font-size: 3.5rem;
    z-index: 1;
}
.columns{
    max-width: 75vw;
    z-index: 1;
}
.portfolio-img{
    width: 25vw;
    cursor: pointer;
    transition: 0.4s all;
}
.portfolio-img:hover{
    filter: opacity(80%);
}
#view{
    z-index: 3;
    position: fixed;
    top: 0px;
    left: 0px;
    background-color: black;
    width: 100vw;
    height: 100vh;
}
#view img{
    width: 100vw;
}
#view h1 a{
    color: white;
    font-weight: bold;
    text-decoration: none;
}
#view h1 a:hover{
    color: rgba(255, 255, 255, 0.575);
    text-decoration: none;
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
    .columns{
        max-width: 90vw;
        z-index: 1;
    }
    .portfolio-img{
        width: 30vw;
        cursor: pointer;
        transition: 0.4s all;
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {

    .columns{
        max-width: 90vw;
        z-index: 1;
    }
    .portfolio-img{
        width: 50%;
        cursor: pointer;
        transition: 0.4s all;
    }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
    #portfolio{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #portfolio h1{
        font-weight: bold;
        font-size: 3rem;
        z-index: 1;
    }
    .columns{
        max-width: 92vw;
        z-index: 1;
    }
    .portfolio-img{
        width: 100%;
        cursor: pointer;
        transition: 0.4s all;
    }
}

