#contact{
    color: white;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../img/bg-5.jpg');
    padding-top: 10rem;
    padding-bottom: 10rem;
}
#contact h1{
    font-weight: bold;
    font-size: 3.5rem;
    z-index: 1;
}
#contact .contact-div{
    background-color: rgba(51,59,80, 1);
    width: 70vw;
    filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
}

.img-div{
    background: url("../img/i-1.jpg");
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;
    background-clip: border-box;
    width: 35vw;
    height: 35vw;
}
.body-div{
    width: 35vw;
    height: 35vw;
    color: rgba(51,59,80, 1);
}
.body-div a{
    text-decoration: none;
    color: rgba(51,59,80, 1);
}
.body-div a:hover{
    text-decoration: none;
    color: rgba(51,59,80, 1);
}
.social-div{
    background-color:rgba(51,59,80, 1);
}
.social-div a{
    margin: 0px 15px;
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
    #contact .contact-div{
        background-color: rgba(51,59,80, 1);
        width: 80vw;
        filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
    }
    .img-div{
        background: url("../img/i-1.jpg");
        background-position: left;
        background-repeat: no-repeat;
        background-size: cover;
        background-clip: border-box;
        width: 40vw;
        height: 40vw;
    }
    .body-div{
        width: 40vw;
        height: 40vw;
        color: rgba(51,59,80, 1);
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    #contact .contact-div{
        background-color: rgba(51,59,80, 1);
