    }
    .body-div{
        width: 45vw;
        height: 50vw;
        color: rgba(51,59,80, 1);
    }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
    #contact{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #contact h1{
        font-weight: bold;
        font-size: 3rem;
        z-index: 1;
    }
    #contact .contact-div{
        background-color: rgba(51,59,80, 1);
        width: 85vw;
        filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
    }
    .img-div{
        background: url("../img/i-1.jpg");
        background-position: left;
        background-repeat: no-repeat;
        background-size: cover;
        background-clip: border-box;
        width: 100%;
        height: 80vw;
    }
    .body-div{
        width: max-content;
        height: max-content;
        color: rgba(51,59,80, 1);
    }
}

