#experience{
    color: white;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../img/bg-4.jpg');
    padding-top: 10rem;
    padding-bottom: 10rem;

    
}
#experience h1{
    font-weight: bold;
    font-size: 3.5rem;
    z-index: 1;
}
#experience .table .row:nth-of-type(n+1) div:first-child{
    border-left: none;
    border-right: 3px solid rgba(0,214,70,1);
}
#experience .table .row:nth-of-type(n+2) div:first-child{
    border-right: 3px solid rgba(0,214,70,1);
    border-left: none;
}

#experience .table .row:nth-of-type(n+1) div:first-child::after{
    content: "";
    background-color: rgba(0,214,70,1);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    right: -12px;
    bottom: 0px;
}
#experience span{
    color: rgba(0,214,70,1);
    font-weight: bold;
    letter-spacing: 0.1rem;
}
#experience p{
    width: 50%;
    line-height: 2.5rem;
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
    
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    #experience p{
        width: 80%;
        line-height: 2rem;
    }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
    #experience{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #experience .col-hide{
        display: none;
    }
    #experience .table .row:nth-of-type(n+2) div:first-child{
        border-right: none;
        border-left: none;
    }
    #experience .table .row:nth-of-type(n+1) div:first-child::after{
        content: "";
        background-color: none;
        width: 0px;
        height: 0px;
        border-radius: 0px;
        position: absolute;
        right: 0px;
        bottom: 0px;
    }
    #experience .work{
        text-align: left;
    }
    #experience p{
        width: 100%;
        line-height: 2rem;
    }
}

@media only screen and (max-width: 576px) {
    #experience{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #experience h1{
        font-weight: bold;
        font-size: 3rem;
        z-index: 1;
    }
    #experience .table .row:nth-of-type(n+2) div:first-child{
        border-right: none;
        border-left: none;
    }
    #experience .table .row:nth-of-type(n+1) div:first-child::after{
        content: "";
        background-color: none;
        width: 0px;
        height: 0px;
        border-radius: 0px;
        position: absolute;
        right: 0px;
        bottom: 0px;
    }
    #experience .col-hide{
        display: none;
    }
    #experience p{
        width: 100%;
        line-height: 2rem;
    }
    #experience .work{
        text-align: left;
    }
}