#navbar{
    position: sticky;
    top: 0px;
    left: 0px;
    z-index: 2;
}
.navbar li{
    margin: 0px 10px;
}
.navbar .active .nav-link{
    color: rgba(0,214,70,1)!important;
}
.navbar .nav-item .nav-link{
    transition: 0.5s all;
}

.navbar .nav-item .nav-link:hover{
    color: rgba(0,0,0,1)!important;
}
