#home{
    color: white;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../img/bg-1.jpg');
    padding-top: 10rem;
    padding-bottom: 10rem;
}
#home .i-am h1{
    font-weight: bold;
    letter-spacing: 0.5rem;
    font-size: 6rem;
    width: 65%;
    z-index: 1;
}
#home .i-am h3{
    letter-spacing: 0.4rem;
    font-size: 2rem;
    width: 65%;
    z-index: 1;
}
#home .i-am span{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color:  rgba(0,214,70,1);
}
#home .about{
    margin-top: 9rem;
}
#home .about-div{
    background-color: rgba(51,59,80, 1);
    width: 70vw;
    padding: 7rem;
    filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
}
#home .about-div .text-center{
    margin-bottom: 5rem;
}
#home .about-div .text-center .logo{
    width: 100px;
    font-size: 4rem;
}
#home .about-div .text-center .logo h1{
    font-size: 4rem;
}
#home .about-div p{
    text-transform: uppercase;
    letter-spacing: 0.3rem;
    line-height: 2.5rem;
    text-align: center;
}
.btn-download{
    border: 2.5px solid rgba(0,214,70,1);
    background-color: transparent;
    padding: 10px 40px;
    transition: 0.4s all;
}
.btn-download:hover{
    background-color: rgba(0,214,70,1);
}
.btn-download a{
    color: white;
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 300;
}
.btn-download a:hover{
    color: white;
    text-decoration: none;
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
    #home .i-am h1{
        font-weight: bold;
        letter-spacing: 0.5rem;
        font-size: 5rem;
        width: 75%;
        z-index: 1;
    }
    #home .i-am h3{
        letter-spacing: 0.4rem;
        font-size: 2rem;
        width: 75%;
        z-index: 1;
    }
    #home .about-div{
        background-color: rgba(51,59,80, 1);
        width: 80vw;
        padding: 6rem;
        filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    #home .i-am h1{
        font-weight: bold;
        letter-spacing: 0.5rem;
        font-size: 4.5rem;
        width: max-content;
        z-index: 1;
    }
    #home .i-am h3{
        letter-spacing: 0.4rem;
        font-size: 1.8rem;
        width: max-content;
        z-index: 1;
    }
    #home .about-div{
        background-color: rgba(51,59,80, 1);
        width: 90vw;
        padding: 5rem;
        filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
    }
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
    #home{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #home .about{
        margin-top: 5rem;
    }
    #home .i-am h1{
        font-weight: bold;
        letter-spacing: 0.5rem;
        font-size: 3.5rem;
        width: max-content;
        z-index: 1;
    }
    #home .i-am h3{
        letter-spacing: 0.4rem;
        font-size: 1.5rem;
        width: max-content;
        z-index: 1;
    }
    #home .about-div{
        background-color: rgba(51,59,80, 1);
        width: 95%;
        padding: 3rem;
        filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
    }
}

@media only screen and (max-width: 576px) {
    #home{
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    #home .about{
        margin-top: 5rem;
    }
    #home .i-am h1{
        padding: 0px 10px;
        font-weight: bold;
        letter-spacing: 0.4rem;
        font-size: 2rem;
        width: max-content;
        z-index: 1;
    }
    #home .i-am h3{
        letter-spacing: 0.2rem;
        text-align: left;
        font-size: 1rem;
        width: max-content;
        z-index: 1;
    }
    #home .about-div{
        background-color: rgba(51,59,80, 1);
        width: 95%;
        padding: 2rem;
        filter: drop-shadow(0 0 0.8rem rgba(0, 0, 0, 0.25));
    }
    #home .about-div p{
        text-transform: uppercase;
        letter-spacing: 0.2rem;
        line-height: 2.3rem;
        text-align: center;
    }
}